.main-header {
  background-image: $color-header;
  position: fixed;
  width: 100%;
  z-index: 10;

  @include vp-1023() {
    position: static;
  }
}

//      windows         
//      scroll-lock-ios.   ,      windows
.scroll-lock .main-header {
  @media (min-width: $vp-1024) {
    width: calc(100% - 17px);
  }
}
